import { Link } from 'react-router-dom'
import mod from './Navigation.module.scss'

const Navigation = () => {
  return (
    <ul className={mod.navigation}>
      <Link className={mod.link} to='menu'></Link>
      <Link className={mod.link} to='menu'></Link>
      <Link className={mod.link} to='menu'> </Link>
      <Link className={mod.link} to='menu'></Link>
      <Link className={mod.link} to='/track'> </Link>
    </ul>
  );
}

export default Navigation